@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
/**/
:root {
 --primary-white: #fdfeff;
  --primary-light-grey: #202124;
  --primary-grey: #4e4b4b;
  --primary-dark-grey: #666666;
  --primary-orange: #e3004d;
  --primary-dark: rgb(32, 33, 36);
  --primary-red: #e3004d;
  --primary-hover-red: #e3004d;
  --primary-black: #000000;
  --primary-transparent-black: rgba(0,0,0,0.7);
  --primary-green: rgb(253, 253, 253);
}

body {
font-family: "Roboto Condensed", sans-serif !important;
background-color: rgb(0, 0, 0) !important;
}


/************************ NAVBAR *********************************/

nav {
  z-index: 2;
}

.logo {
  width: 16.25rem;
}
.navbar{
  background-color: #202124;
}


/*----- MENU LINKS --------*/
nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
  font-weight: bold;
  font-family: 'Work Sans', sans-serif;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fdfeff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgb(32, 33, 36);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

@media(max-width: 768px) {
 .navbar-brand {
   width: 7rem;
   color: #e3004d;

 }
}

.navbar-nav > li > a.active,  .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #e3004d;
  border-top: 0.1875rem solid var(--primary-red);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #e3004d;
  border-top: 0.1875rem solid var(--primary-red);
  
}


/************************ HEADER *********************************/

.header-wraper {
  position: relative;
  background: url(/portfolio-project-tutorial/static/media/bg44.292fa466.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
  color: #e3004d;
  color: var(--primary-red);
  text-transform: uppercase;
  font-family: 'Times New Roman', Times, serif;
}




/*-------- TYPED TEXT ---------------*/
.typed-text {
  font-size: 2rem;
  color: #fdfeff;
  color: var(--primary-white);
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;

  }
}

