/*-------- PARTICLES JS ---------------*/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/************************ ABOUT ME *********************************/
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid #e3004d;
  border: 1px solid var(--primary-red);
}

.about-heading {
  color: #e3004d;
  color: var(--primary-red);
  text-transform: uppercase;
}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}

.abt-text{
  color: #fdfeff;
  color: var(--primary-white);
  font-size: 1.3rem;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
 
}



/************************ SERVICES *********************************/
.services {
  text-align: center;
  padding-bottom: 2rem;
  background: #202124;
  background: var(--primary-light-grey);
}

.services h1 {
  color: #e3004d;
  color: var(--primary-red);
  text-transform: uppercase;
  font-family: 'Times New Roman', Times, serif;
}

.pcolor-edu{
  color: white;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #e3004d;
  background: var(--primary-red);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
  cursor: pointer;
}

.services .icon {
  font-size: 1.4rem;
  color: #fdfeff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  
}

.services .box {
  height: 16rem;
  border-bottom: 0.3125rem solid #e3004d;
  border-bottom: 0.3125rem solid var(--primary-red);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #fdfeff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.services .box:hover {
  background: #e3004d;
  background: var(--primary-red);
  border-bottom: 0.3125rem solid rgb(32, 33, 36);
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.services .box:hover .icon {
  color: rgb(32, 33, 36);
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: #fdfeff;
  background: var(--primary-white);
}

.services .box:hover p {
  color: #fdfeff;
  color: var(--primary-white);
}


/************************ EXPERIENCE *********************************/

.experience {
  padding: 0.7rem;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #e3004d;
  color: var(--primary-red);
  font-family: 'Times New Roman', Times, serif;
}

.experience-text{
  color: #ffffff;
}
.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #4e4b4b;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}


.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;;
  border: 0.3125rem solid rgb(32, 33, 36);;
  border: 0.3125rem solid var(--primary-dark);
  background: #e3004d;
  background: var(--primary-red);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #e3004d;
  color: var(--primary-red);
  font-weight: 900;
}

/*------------ MEDIA ------------------*/
@media(max-width: 768px) {
.experience .container {
  padding-left: 0;
}

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  
  .timeline-block-left {
    float: none;
    direction: ltr; 
    width: 100%;
  }
}

/************************ PORTFOLIO *********************************/
.portfolio-wrapper {
  background: #202124;
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: #e3004d;
  color: var(--primary-orange);
  font-family: 'Times New Roman', Times, serif;
}

.portfolio-image {
  width: 15rem;
  border: 1px solid #e3004d;
  border: 1px solid var(--primary-red);
  padding: 0 0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

/*------------ OVERFLOW BOX ------------------*/
.overflow {
  position: absolute;
   opacity: 0;
   background: #000000;
   background: var(--primary-black);
   width: 15rem;
   height: 8rem;
   top: 0;
  border: 1px solid #e3004d;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #e3004d;
  color: var(--primary-red);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.popupbox-wrapper {
  margin-top: 7rem;
}

/*------------ OVERFLOW BOX ------------------*/
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer; 
  color: #e3004d; 
  color: var(--primary-red);
}

.hyper-link:hover {
  color: #e3004d;
  color: var(--primary-red);
}

/*------------ MEDIA ------------------*/
@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 570px) {
.popupbox-wrapper {
  height: 100%;
}
  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }
    .portfolio-image-popupbox {
      width: 30%;
    }
  }

/************************ TESTIMONIALS *********************************/
.testimonials {
  background-image: url(/portfolio-project-tutorial/static/media/testimonials-bg.75612be7.jpg);
  background-size: cover;
  background-color: #000000;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1 {
  color: #e3004d;
  color: var(--primary-red);
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonials-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: rgba(0,0,0,0.7);
  background: var(--primary-transparent-black); 
}

.myCarousel {
  color: #fdfeff;
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;
}

.myCarousel h3 {
  color: #e3004d;
  color: var(--primary-red);
  font-weight: 600;
  text-transform: uppercase;
}

.myCarousel p {
  color: #4e4b4b;
  color: var(--primary-grey);
}

.carousel .slide img {
  width: 10rem !important;
  border-radius: 50%;
}

.carousel .slide {
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;
}

/************************ CONTACTS COMPONENT *********************************/
.contacts {
  background: rgb(32, 33, 36);
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: #e3004d;
  color: var(--primary-red);
  padding: 1.3rem 0;
  text-transform: uppercase;
  font-family: 'Times New Roman', Times, serif;
}

.contacts p {
  color: #666666;
  color: var(--primary-dark-grey);
  font-size: 1.2rem;
}

/*------------------ INPUTS -------------------------------*/
.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #e3004d;
  color: var(--primary-red);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #e3004d;
  color: var(--primary-red);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
  border: 1px solid #e3004d;
  border: 1px solid var(--primary-red);
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #e3004d;
  background: var(--primary-red);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #fdfeff;
  color: var(--primary-white);
  background: #e3004d;
  background: var(--primary-hover-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: rgb(253, 253, 253);
  color: var(--primary-green);
}

/*---------------- MEDIA -----------------------*/
@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  
  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/************************ FOOTER COMPONENT *********************************/
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: #e3004d;
  color: var(--primary-red);
  cursor:pointer;
}

.footer p {
  margin-bottom: 0;
}

.btnlinkicon{
 
  padding-right: 0.2rem;
  border: none;
  background-color:transparent;

}

.linkicon{
  border-radius: 0.2rem;
}

.giticon{
  color: white;
}
.giticon:hover{
  color: #e3004d;
}





